

.product-title-content{
    height: 24vh;
    width: 100%;
    color:#FF8C00;
    text-shadow:-1px 2px 2.5px rgba(0, 0, 0, .15), 0 0 14px rgba(0, 0, 0, .02), 0 0 1px rgba(0, 0, 0, .1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.product-title{
    font-size: 3vw;
    font-weight: bold;
    margin-bottom: 10px;
}
.filter-button-container{
    display: none;
}
.filter-dropdown-container{
   display: flex;
}
.filter-button{
    position: relative;
    font-size: 16px;
    padding: 0px 5px;
    background: transparent;
    border: none;
    margin: 0px 15px;
    color: #000000;
    box-sizing: border-box;
}
.filter-button:hover{
    color: #FF8C00;
}
.filter-button::after{
    content: "";
    position: absolute;
    background-color: #FF8C00;
    height: 3px;
    width: 0%;
    left: 0%;
    bottom:-10px;
}
.filter-button:hover:after{
    width: 100%;
    transition: 0.3s;
}

#product {
    color: #000000;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   background-color: #ffff;
}

#descTextStyle {
    margin-bottom: 0.5rem;
}

#priceTextStyle {
    margin: 0rem 0rem 0.7rem 0rem;
    font-weight: bold;
}
.col-3 {
    flex: 0 0 auto;
    width: 50%;
}
#buttonStyle{
    height: 2.5rem;
    width: 9rem;
    color: #000000;
    font-weight: bold;
    background-color: #ff8c00;
    border-radius: 0px;
    box-shadow: -1px 2px 2.5px rgba(0, 0, 0, .15), 0 0 14px rgba(0, 0, 0, .02), 0 0 1px rgba(0, 0, 0, .1);
}
 
#buttonStyle:hover {
    background-image: linear-gradient(to right, #c0392b,#8e44ad);
    color: #ffff;
}
.row{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.card{
    width: 17rem;
    border-radius: 0%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 0.1px solid #00000023;
    padding: 0.5rem;
}
.card-body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 0rem 0rem 0.5rem 0rem;  
}
.card-title{
    margin: 0.5rem;
}
.animate__zoomIn {
    height: auto;
    max-width: 100%;
    text-align: center;
}

